:root {
   --yellow: #f5c32c;
   --orange: #fca61f;
   --black: #242d49;
   --gray: rgba(36, 45, 73, 0.65);
   --profileShadow: 0px 4px 17px 2px rgba(0, 0, 0, 0.25);
   --hrColor: #cfcdcd;
   --cardColor: rgba(255, 255, 255, 0.64);
   --buttonBg: linear-gradient(98.63deg, #4caefe 0%, #226fae 100%);
   --inputColor: rgba(40, 52, 62, 0.07);
   --photo: #4CB256;
   --video: #EF5757;
   --location: #be58b7;
   --shedule: #E1AE4A;
}

@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;600;700&display=swap');

* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

li {
   list-style: none;
}

a {
   text-decoration: none;
}

button {
   outline: none;
   border: none;
}

body {
   font-family: 'Josefin Sans', sans-serif;
   overflow: hidden;
}

.container {
   width: 100%;
   height: 100vh;
   background-image: linear-gradient(45deg, #efefef 0%, #e0dddd 50%);
}

.blur {
   position: absolute;
   width: 22rem;
   height: 14rem;
   border-radius: 50%;
   background: #a6ddf0;
   filter: blur(72px);
}

.content {
   width: 100%;
   height: 100%;
   display: grid;
   grid-template-columns: 49% 49%;
   padding: 3% 10%;
   border-radius: 1rem;
}

.form-container {
   background: url('./asset/lake-background.jpg') no-repeat;
   background-size: cover;
   z-index: 100;
   border-radius: 1rem;
   border-top-right-radius: 0rem;
   border-bottom-right-radius: 0rem;
   box-shadow: var(--profileShadow);
   display: flex;
   flex-direction: column;
   gap: 16rem;
}

.info-container {
   background: var(--cardColor);
   box-shadow: var(--profileShadow);
   z-index: 100;
   border-radius: 1rem;
   border-top-left-radius: 0rem;
   border-bottom-left-radius: 0rem;
}

.name {
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 3%;
}

.logo {
   font-weight: bold;
   font-size: 1.25rem;
   background: -webkit-linear-gradient(#A4B7B1, #095F62);
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
}

.city {
   display: inline-flex;
}

.city>svg {
   margin-right: 10px;
   height: 20px;
}

.logo,
.city {
   background-color: var(--cardColor);
   padding: 2%;
   display: flex;
   align-items: center;
   justify-content: center;
   border-radius: 0.75rem;
}

.search {
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 1rem;
}

.search h2 {
   font-size: 1.5rem;
   font-weight: bold;
   color: white;
}

.search hr {
   border-bottom: 3px solid #ffff;
   width: 30%;
}

.search-bar {
   display: flex;
   width: 100%;
   padding: 0% 28%;
}

.search-bar>input {
   background-color: var(--cardColor);
   width: 100%;
   height: 30px;
   padding-left: 10px;
   border: none;
   outline: none;
   color: #ffff;
   font-size: 1rem;
   border-radius: 5px;
   border-top-right-radius: 0px;
   border-bottom-right-radius: 0px;
}

.s-icon {
   display: flex;
   align-items: center;
   justify-content: center;
   background: linear-gradient(106.23deg, #A4B7B1, #095F62 100%);
   border-radius: 5px;
   border-top-left-radius: 0px;
   border-bottom-left-radius: 0px;
   padding: 8px;
   color: white;
   transition: all 0.5s ease;
}

.s-icon:hover {
   cursor: pointer;
   background: linear-gradient(106.23deg, #226fae, #226fae 100%);
   transform: scale(1.05);
}

.info-container {
   padding-left: 5%;
}

.nodata {
   margin-top: 20px;
   color: darkgray;
   display: flex;
   align-items: center;
   justify-content: center;
}

.summary {
   display: flex;
   /* grid-template-columns: repeat(2, 40%); */
   justify-content: space-around;
   align-items: center;
   padding: 1rem;
   background-color: var(--cardColor);
   box-shadow: var(--profileShadow);
   border-radius: 1rem;
   margin: 1rem;
}

.title {
   margin-top: 3rem;
}

@media only screen and (max-width: 1024px) {
   .content {
      display: grid;
      grid-template-columns: 99%;
      gap: 2rem;
      overflow: auto;
      padding: 5%;
      font-size: 1.5rem;
   }

   .content::-webkit-scrollbar {
      width: 0 !important;
      display: none;
   }

   .form-container {
      border-radius: 1rem;
   }

   /* .info-container{    margin-top: 2rem;  } */
   .logo {
      font-size: 1.5rem;
   }

   .city {
      font-size: 1.25rem;
   }

   .search h2 {
      font-size: 1.75rem;
   }

   .search-bar {
      display: flex;
      width: 100%;
      padding-bottom: 10%;
   }

   .info-container {
      border-radius: 1rem;
   }

   .nodata {
      font-size: 1.5rem;
   }

   .clouds {
      font-size: 1.45rem;
      width: 55%;
   }

   .des {
      font-size: 1.5rem;
   }

   .more-info {
      font-size: 1.25rem;
      padding-right: 0.25rem;
      border-left: 1px solid var(--hrColor);
      padding-left: 75px;
      padding-right: 1rem;
   }

   h1 {
      font-size: 2rem;
   }

   .summary {
      display: grid;
      grid-template-columns: repeat(2, 49%);
      align-self: center;
      gap: 1rem;
   }

   .search-bar>input {
      padding: 7%;
      font-size: 1.25rem;
   }

   .s-icon {
      padding: 8px;
   }
}

@media only screen and (max-width: 768px) {
   .logo {
      font-size: 1.25rem;
   }

   .city {
      font-size: 1rem;
   }

   h2 {
      font-size: 1.1rem;
   }
}

@media only screen and (max-width: 480px) {
   .content {
      display: grid;
      grid-template-columns: 99%;
      gap: 2rem;
      overflow: auto;
      padding: 5%;
   }

   .content::-webkit-scrollbar {
      width: 0 !important;
      display: none;
   }

   .form-container {
      border-radius: 1rem;
   }

   /* .info-container{    margin-top: 2rem;  } */
   .logo {
      font-size: 0.95rem;
   }

   .city {
      font-size: 0.95rem;
   }

   .search h2 {
      font-size: 1rem;
   }

   .search-bar {
      display: flex;
      width: 100%;
      padding-bottom: 10%;
   }

   .info-container {
      border-radius: 1rem;
   }

   .nodata {
      font-size: 0.75rem;
   }

   .clouds {
      font-size: 0.85rem;
      width: 55%;
   }

   .des {
      font-size: 0.65rem;
   }

   .more-info {
      font-size: 0.75rem;
      padding-right: 0.25rem;
      border-left: 1px solid var(--hrColor);
      padding-left: 10px;
      padding-right: 0.5rem;
   }

   h1 {
      font-size: 1.5rem;
   }

   .summary {
      display: grid;
      grid-template-columns: repeat(2, 49%);
      align-self: center;
   }
}